import express from 'express';
import { Joi, celebrate } from 'celebrate';
import getOTP from '../controllers/otp_get';
import { baseUrl } from '../baseurl';

const router = express.Router();

const userSchema = {
  query: {
    mobileNumber: Joi.string().required(),
  },
};

/**
 * @swagger
 * /auth/otp:
 *   get:
 *     tags:
 *       - Authentication
 *     description: Returns a jwt token if login is successful
 *     consumes:
 *       - application/json
 *     parameters:
 *      - in: "query"
 *        name: mobileNumber
 *        description: "Mobile number to send OTP"
 *        required: true
 *        schema:
 *           type: object
 *           properties:
 *              mobileNumber :
 *                  type: string
 *     produces:
 *       - application/json
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *              message:
 *                  type: string
 *       400:
 *         description: Returns 400 if it fails to send the OTP.
 *         schema:
 *           type: object
 *           properties:
 *              statusCode:
 *                  type: number
 *                  example: 400
 *              error:
 *                  type: string
 *              message:
 *                  type: string
 */

router.get(`${baseUrl}/otp`, celebrate(userSchema), getOTP);

export default router;
